.addNoteBody {
    max-width: 70vw;
    margin-left: auto;
    margin-right: auto;
}

.breadcrumb {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 1rem;
    margin-top: 20px;
}

.bc-p1 {
    margin: 0px 10px 0px 0px;
    color: yellow;
}

.bc-p1:hover {
    cursor: pointer;
}

.bc-sep {
    margin: 0px 10px 0px 0px;
}

.bc-p2 {
    margin: 0px 10px 0px 0px;
}

.add-note-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.5rem;
    font-weight: 400;
}

.add-form {
    margin-bottom: 30px;
}

.add-title {
    width: 98%;
    height: 40px;
    background-color: white;
    border-radius: 10px 10px 0px 0px;
    color: black;
    font-family: unbounded;
    padding-left: 2%;
    border: 2px solid yellow;
}

.add-body {
    width: 98%;
    height: 50vh;
    background-color: white;
    border-radius: 0px 0px 10px 10px;
    font-family: Poppins;
    font-size: 1rem;
    color: black;
    padding-left: 2%;
    padding-top: 20px;
    border: 1px solid yellow;
}

.add-submit {
    color: black;
    font-family: unbounded;
    width: 130px;
    height: 35px;
    border-radius: 10px;
    border: 0px;
    background-color: yellow;
}

.add-submit:hover {
    cursor: pointer;
    background-color: rgb(255, 255, 55)
}

.deleteStarter {
    margin: 0px 5px;
    background-color: yellow;
    padding: 4px 10px;
    border-radius: 20px;
    font-size: 1rem;
    font-weight: 600;
    color: black;
    transition: all 0.2s linear;
}

.deleteStarter:hover {
    background-color: rgb(172, 172, 2);
    cursor: pointer;
}